import React, { Component } from 'react';
import goodList from '../goodlist';
import { Button,Icon } from 'antd';
import { Link } from 'react-router-dom';
import styles from '@/styles/common.css';
export default class GoodDetail extends Component {
    constructor(props) {
        super(props)
      this.state = {
         img:""
        }
    }
   
    componentDidMount(){
      let img="";
      goodList.map(item=>{
       if(item.id==this.props.match.params.id){
         img=item.imgsrc
       }
      });
      this.setState({
        img
      })

    }


  render() {
        return (            
          <div style={{ height: "100%" }}>
              <Icon type="arrow-left" onClick={()=>this.props.history.push('/')} style={{fontSize:30,marginTop:"20px",marginLeft:"20px"}}/>
              <img src={this.state.img} style={{width:"100%",marginBottom:"20px"}}/>      
              <div className={styles.header} style={{zIndex:'9999'}}>
                <div className={styles.btnList}>
                <Link  to="/" >店铺</Link>
                <Link to="/">客服</Link>
                <Link to="/" >收藏</Link>
                <Link to="/" style={{marginRight:'0'}}>加入购物车</Link>
                <Link to="/" style={{marginRight:'0'}}>购买</Link>
               </div>
		         	</div>
          </div>
        )   
    }
}


